import style from './App.module.css';
import React, { useState, useEffect, useRef, } from 'react';
import FRTimer from './components/intervalTimer/FRTimer';
import MsmTimer from './components/intervalTimer/MsmTimer';
import CETimer from './components/intervalTimer/CETimer';
import PageNotFound from './components/pageNotFound/PageNotFound';
const App = () => {

  const [tabIdx, setTabIdx] = useState(1);

  const tabData = (idx) => {
    switch (idx) {
      //NewVer
      case 1:
        return <MsmTimer />

      case 2:
        return <FRTimer />

      case 3:
        return <CETimer />

      default:
        break;
    }
  }
  // console.log(document.location.ancestorOrigins.first)
  // console.log(document.location.ancestorOrigins)
  return (
    <div>
      {/* <div className={style.menuBar}>
        <ul className={style.tabs}>
          <li style={{ backgroundColor: tabIdx == 1 ? '#222' : '' }} onClick={() => setTabIdx(1)}>MSM</li>
          <li style={{ backgroundColor: tabIdx == 2 ? '#222' : '' }} onClick={() => setTabIdx(2)}>FRT</li>
          <li style={{ backgroundColor: tabIdx == 3 ? '#222' : '' }} onClick={() => setTabIdx(3)}>CE</li>
        </ul>
      </div> */}
      <div >
        {/* {tabData(tabIdx)} */}
        {typeof document.location.ancestorOrigins[0] != "undefined" && document.location.ancestorOrigins[0].includes("xingmove.com") ?
          <FRTimer />
          : <PageNotFound />
        }

        {/* <iframe src='http://13.125.227.52/xmove/'></iframe> */}
      </div>
    </div >
  );
}

export default React.memo(App);
